.alert-main {
    position: fixed;
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    margin: auto;
    background-color: rgb(156, 148, 148);
    color: white;
    width: 100%;
    height: 100%;
    align-items: center;
    background: rgba(0, 0, 0, 0.59);
    font-family: 'Roboto Slab', serif;
}

.alert-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 25%;
    min-height: 30%;
    border-radius: 55px;
    border: 2px solid white;
    box-sizing: border-box; 
    background-color: rgb(0, 0, 0);
}

.alert-div p {
    text-align: center;
    width: 50%;
    margin: 10px auto;
}


@media screen and (max-width: 700px) {
    .alert-div {
        min-height: 50%;
        min-width: 100%;
    }

    .alert-div h1 {
        font-size: 25px;
    }
}