* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -moz-box-sizing: padding-box;
    -webkit-box-sizing: padding-box;

}

html, body {
    zoom: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 100vh; 
    overflow-x: hidden;
    overflow-y: hidden;
    font-family: 'Big Shoulders Display', cursive;
    letter-spacing: 2px;
}


@media screen and (max-width: 700px) { 
    .App {
        min-width: 100%;
        overflow-x: hidden;
    }
    .body {
        min-width: 100%;
        overflow-x: hidden;

    }
}