.logged-user-main {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 2px solid rgb(17, 17, 17);
    border-radius: 12px;
    cursor: pointer;
    transition: 0.5s;
    padding: 2px;
    background-color: rgb(18, 42, 42);
    height: 125px;
    width: 100%;
}


.logged-user-main:hover{
    background-color: rgba(229, 241, 241, 0.966);
    color: black;
    transition: 0.5s;
}

.logged-user-photo {
    width: 80px;
    min-height: 80px;
    padding: 2px;
    border-radius: 15px;
}

.photo-name {
    display: flex;
    flex-direction: row;
    font-size: 20px;
}

.photo-name p {
    margin: auto;
    font-weight: bold;
}

.details {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    width: 100%;
    margin: 0 auto;
}
.details p{
    text-align: center;
    padding: 2px;
    font-size: 18px;
    font-weight: bold;
    margin: 0 auto;
    width: 100%;
}

.logged-user {
    background-color: rgba(16, 16, 17, 0.616);
    border-color: rgba(0, 0, 0, 0.89);
}